<template>
    <div class="search-box">
      <div class="search-input fl">
        <input type="text" ref="query"  v-model="query" :placeholder="placeholder" class="fl" maxlength="15">
        <!--<i @click="onClear" v-show="query" class="icon-close">删除</i>-->
      </div>
      <div class="search-btn fr"  @click="onSearch"><span class="icon"></span></div>
    </div>
</template>

<script>
import { debounce } from '@/assets/js/util'
export default {
  name: 'BaseSearchBox',
  props: {
    placeholder: {
      type: String,
      default: '请输入你想搜索的关键字'
    },
    matchlist: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      query: ''
    }
  },
  methods: {
    onClear () {
      this.query = ''
    },
    onSearch () {
      this.$emit('search', this.query)
    },
    blur () {
      this.$refs.query.blur()
    }
  },
  created () {
    this.$watch('query', debounce((newQuery) => {
      // this.$emit('query', newQuery)
      this.$emit('matchSearch', newQuery)
    }, 200))
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
   .search-box{
     width:480px;
     height:60px;
     line-height: 60px;
     border-left: 2px solid #15C7C1;
     border-top: 2px solid #15C7C1;
     border-bottom: 2px solid #15C7C1;
     border-radius: 30px;
     .search-input{
       width:398px;
       position: relative;
       input{
         width: 100%;
         height:60px;
         line-height: 60px;
         border-radius: 30px;
         font-size: 16px;
         color: $color-text;
         padding-left: 20px;
       }
       .icon-close{
          position: absolute;
         right: 0;
       }
     }
     .search-btn{
       cursor: pointer;
       background-image: linear-gradient(-180deg, #2ADAD4 0%, #15C7C1 100%);
       border-radius: 30px;
       width: 60px;
       height: 60px;
       text-align: center;
       .icon{
         background: url("../assets/images/icon_search.png") no-repeat;
         background-size: 26px 26px;
         width:26px;
         height:26px;
         display: block;
         margin:0 auto;
         margin-top: 17px;
       }

     }
   }
  ::-webkit-input-placeholder { /* WebKit browsers */
    color: $color-text;
  }

  ::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: $color-text;
  }

  :-ms-input-placeholder { /* Internet Explorer 10+ */
    color: $color-text;
  }
</style>
