<template>
  <div class="index-container">
    <button @click="openCenter()">center</button>
    <base-slider class="crousel"
                 :list="imgList"
                 :height="640"
                 @sliderClick="sliderClick">
    </base-slider>
    <!--<base-page :total="110"></base-page>-->
    <div class=" search-container">
      <div class="search-wrapper">
        <p class="title">从这里开始</p>
        <p class="tips">输入您需要的保障，我们会为您推荐合适的产品</p>
        <base-search-box @search="onSearch" ></base-search-box>
        <div class="hot-search clearfix">
          <p class="hot-title fl">或换一个推荐关键词：</p>
          <ul class="hot-list fl clearfix">
            <li @click="goList">旅游险</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="layout">
      <div class="layer layer-categogy">
        <p class="title">
          产品分类
        </p>
        <ul class="product-category clearfix">
          <li>
            <router-link tag="div" to="/37duc/product/list?type=1">
            <img src="../assets/images/category_yw.png" alt="">
            <p>意外伤害</p>
            </router-link>
          </li>
          <li>
            <router-link tag="div" to="/37duc/product/list?type=2">
            <img src="../assets/images/category_yl.png" alt="">
            <p>医疗保险</p>
            </router-link>
          </li>
          <li>
            <router-link tag="div" to="/37duc/product/list?type=3">
            <img src="../assets/images/category_jb.png" alt="">
            <p>疾病保险</p>
            </router-link>
          </li>
          <li>
            <router-link tag="div" to="/37duc/product/list?type=4">
            <img src="../assets/images/category_hl.png" alt="">
            <p>护理保险</p>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="layer layer-recommend">
        <p class="title">
          产品推荐
        </p>
        <div class="product clearfix">
          <img src="../assets/images/tj.png" alt="">
          <div class="product-wrapper">
            <div class="list clearfix">
              <div class="part-lt fl">
                <div class='pName'>{{productDetails.product.name}}</div>
                <div class="pMsg">
                  <span class="condition-item" v-for="(item,index) in productDetails.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>
                </div>
                <div class="marks clearfix">
                  <div class='mark fl'>
                    <span class="fl"><i></i>{{productDetails.product.feature1}}</span>
                    <span class="fl"><i></i>{{productDetails.product.feature2}}</span>
                    <span class="fl"><i></i>{{productDetails.product.feature3}}</span>
                  </div>
                </div>
                <div class="characteristics">
                  <div class="character clearfix" v-for="(item,index) in productDetails.product.parameter_values[0].entries" :key="index">
                    <span class="fl">{{item.name}}</span>
                    <span class="fr">{{item.limit}}</span>
                  </div>
                </div>
                <div class="logo"> <img :src="baseSrc+productDetails.product.brandLogo" alt="公司logo"></div>
              </div>
              <div class="part-rt">
                <div class="ctx">
                  <div class="price"><span class="p">{{productDetails.product.price}}</span><span class="b">元起</span></div>
                  <div class="toDetail" @click="goDetail(1)">查看详情</div>
                  <slot></slot>
                </div>
              </div>
            </div>

          </div>
        </div>

      </div>
      <div class="layer layer-company">
        <p class="title">
          合作品牌
        </p>
        <ul class="coper-company clearfix">
          <li><img src="../assets/images/pa.png" alt=""></li>
          <li><img src="../assets/images/al.png" alt=""></li>
          <li><img src="../assets/images/hz.png" alt=""></li>
          <li><img src="../assets/images/lh.png" alt=""></li>
          <li><img src="../assets/images/tpy.png" alt=""></li>
          <li><img src="../assets/images/rm.png" alt=""></li>
          <li><img src="../assets/images/hj.png" alt=""></li>
        </ul>
      </div>
    </div>

    <!-- <p v-show="show" class="show">内容展示</p>
     <span>时间:{{time | datefmt}}</span>
     <router-link to="/login">测试</router-link>-->
    <!--<v-distpicker :placeholders="placeholders"></v-distpicker>-->
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import baseSlider from '@/components/BaseSlider.vue'
import baseSearchBox from '@/components/BaseSearchBox.vue'
import { getLocalStorage } from '@/assets/js/localStorage'
// import VDistpicker from 'v-distpicker'
export default {
  name: 'home',
  data () {
    return {
      baseSrc: '',
      memberId:'',
      imgList: [
        { src: require('../assets/images/c.jpg') }
      ]
    }
  },
  mounted () {
    this.getTjDetail()
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
  },
  methods: {
    openCenter(){
      this.$toast.center('center');
    },
    getTjDetail () {
      this.memberId = getLocalStorage('userInfo').id
      this.GetDetailList({
        productId: 1,
        memberId: this.memberId
      })
    },
    onSearch (query) {
      if (query) {
        this.$router.push({
          path: '/37duc/product/list/1'
        })
      }
    },
    goList () {
      this.$router.push({
        path: '/37duc/product/list/1'
      })
    },
    onMatchSearch (newQuery) {
      console.log('newQuery', newQuery)
      // this.matchlist=
    },
    sliderClick (i) {
      console.log(i)
    },
    goDetail (id) {
      this.$router.push({
        path: `/37duc/product/detail/${id}`
      })
      // this.GetDetailList(1)
    },
    ...mapActions(['GetDetailList'])
  },
  computed: {
    ...mapGetters(['productDetails'])
  },
  components: {
    baseSlider,
    baseSearchBox
    // VDistpicker
  }
}
</script>
<style lang="scss" scoped>
  @import "@/assets/styles/mixin.scss";

  .index-container {
    background: #fff;
    position: relative;
    top: -90px;
