        height: 326px;
        position: absolute;
        z-index: 101;
        top: 0px;
        right: 0;
        background: #fff;
        .title {
          font-size: 50px;
          font-weight: bold;
          color: $color-text;
          .tips {
            margin: 30px 0 30px;
          }
        }
        .tips {
          margin: 25px 0 30px;
          font-size: 16px;
          color: $color-text;
        }
        .hot-search {
          margin-top: 30px;
          .hot-title {
            font-size: 16px;
