      margin-top: -326px;
      padding: 50px 0 100px;
      .product-category {
        margin-top: 40px;
        li {
          float: left;
          padding: 0 83px;
          cursor: pointer;
          img {
            width: 108px;
            height: 108px;
          }
          p {
            font-size: 12px;
            font-size: 18px;
            color: $color-text;
            text-align: center;
          }
        }
      }
    }
    .layer-recommend {
      padding-bottom: 100px;
