                white-space: nowrap;
                .condition-item {
                  font-size: 14px;
                  color: #B6BBBB;
                  &:not(:last-child):after {
                    content: '|';
                  }
                }
              }
              .marks {
                margin: 12px -40px 12px 0;
                .mark {
                  margin-right: 40px;
                  i {
                    display: inline-block;
