                height: 40px;
                overflow: hidden;
                /*background: rgba(0, 166, 0, .6);*/
                img {
                  float: right;
                  width: 70px;
                  height: 40px;
                }
              }
            }
            .part-rt {
              position: relative;
              overflow: hidden;
              float: right;
              /*background-color: rgba(241, 244, 244, .5);*/
              border-left: 1px solid #F1F4F4;
              box-sizing: border-box;
              height: 100%;
              width: 200px;
              .ctx {
                position: absolute;
                top: 50%;
