                right: 0;
                margin: -58px 0;
                .price {
                  margin: -3px 0 49px;
                  text-align: center;
                  white-space: nowrap;
                  .p {
                    line-height: 33px;
                    height: 33px;
                    display: inline-block;
                    font-size: 30px;
                    color: $color-active-text;
                  }
                  .b {
                    line-height: 20px;
                    font-size: 12px;
                    height: 20px;
                    display: inline-block;
                    color: #D5D8D8;
                  }
                  .p, .b {
                    vertical-align: bottom;
                  }
                }
                .toDetail {
                  width: 130px;
                  height: 40px;
                  line-height: 40px;
                  background-color: $color-active-text;
                  color: #FFFFFF;
                  font-size: 16px;
                  text-align: center;
                  border-radius: 20px;
                  margin: 0 auto;
                  cursor: pointer;
                  letter-spacing: 1px;
                }
